@use '../../../scss/Foundation/mixin';

.p-article--about-us__img__left-flag{
    background: url(../images/left-flag.png) no-repeat;
    background-size: contain;
    min-width: 11rem;
    min-height: 11rem;
    position:absolute;
    left:5%;
    top:30%;
    transform: rotateZ(-25deg);
    @include mixin.mq(tb){
        top:calc(150/1440*100vw);
    }
}