@use '../../../scss/Foundation/mixin';

.p-article--work__full-order__text{
    font-size:calc(16/1440*100vw);
    text-align: left;
    font-family:tsukushigothic,ms gothic,ヒラギノ角ゴ pro w3,hiragino kaku gothic pro,osaka,sans-serif;
    margin-top:2rem;
    @include mixin.mq(tb){
        font-size:calc(16/1023*100vw);
    }
    @include mixin.mq(sp){
        font-size:calc(14/375*100vw);
    }
}