@use '../../../scss/Foundation/mixin';

.p-article--new-release__cat__right{
    background:url(../images/right-cat.gif) no-repeat;
    width:30rem;
    height:30rem;
    position: absolute;
    bottom: 0%;
    left:70%;
    background-size: contain; 
    @include mixin.mq(sp){
        width:15rem;
        height: 15rem;
        left:60%;
    }
    @include mixin.mq(tb){
        width:calc(300/1023*100vw);
        height:calc(300/1023*100vw);
    }
}