@use '../../../scss/Foundation/mixin';

.p-article--gallery__red-cloud{
    background: url(../images/red-cloud.png) no-repeat;
    width:8rem;
    height:8rem;
    background-size: contain;
    position: absolute;
    left: 5%;
    top:10%;
    @include mixin.mq(sp){
        width: calc(90/375*100vw);
        height:calc(90/375*100vw);
        top:calc(110/375*100vw);
    }
}